.dz-box.gallery {
  position: relative;
}

.dz-box.gallery .content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  transition: all 0.4s;
}

.dz-box.gallery .content:after {
  content: '';
  position: absolute;
  height: 100%;
  opacity: 0;
  z-index: 0;
  width: 100%;
  bottom: 0px;
  left: 0px;
  -webkit-transition: all 0.5s;
  -ms-transition: all 0.5s;
  transition: all 0.5s;
  background: #000;
}

.dz-box.gallery .content .view-btn {
  z-index: 1;
  position: absolute;
  opacity: 0;
  top: 50%;
  left: 50%;
  background-color: #fff;
  -webkit-transition: all 0.5s;
  -ms-transition: all 0.5s;
  transition: all 0.5s;
  transform: translate(-50%, -50%) skewX(-12deg) translateY(2px);
  cursor: pointer;
  display: block;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  font-size: 18px;
  color: var(--title);
}

.dz-box.gallery .content .view-btn i {
  transform: skewX(12deg);
}

.dz-box.gallery:hover .content .view-btn {
  opacity: 1;
}

.dz-box.gallery:hover .content:after {
  opacity: 0.5;
}

.gym-time-container {
  height: 73.5vw;
  max-height: 535px;
  max-width: 500 px;
  width: 98vw;
  position: relative;
  overflow: hidden;
}

.gym-time-container figure {
  background: url("/src/assets/images/after-before/before.jpg") no-repeat center center;
  background-size: cover;
  position: absolute;
  margin: 0;
  height: 100%;
  width: 100%;
}

.gym-time-container figure div:first-of-type {
  background-image: url("/src/assets/images/after-before/after.jpg");
  background-size: cover;
  position: absolute;
  height: 100%;
  width: 50%;
  box-shadow: 0 5px 10px -2px rgba(0, 0, 0, 0.3);
  bottom: 0;
}

.gym-time-container figure div:first-of-type::before,
.gym-time-container figure div:first-of-type::after {
  content: "";
  position: absolute;
  right: -2px;
  width: 4px;
  height: calc(50% - 25px);
  background: white;
  z-index: 3;
}

.gym-time-container figure div:first-of-type::before {
  top: 0;
}

.gym-time-container figure div:first-of-type::after {
  bottom: 0;
}

.gym-time-container figure div:last-of-type {
  border-radius: 100%;
  background: white;
  position: absolute;
  height: 50px;
  width: 50px;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  z-index: 1;
}

.gym-time-container figure div:last-of-type::before,
.gym-time-container figure div:last-of-type::after {
  content: "";
  width: 0;
  height: 0;
  border: 6px inset transparent;
  position: absolute;
  top: 50%;
  margin-top: -6px;
}

.gym-time-container figure div:last-of-type::before {
  border-right: 6px solid grey;
  left: 50%;
  margin-left: -17px;
}

.gym-time-container figure div:last-of-type::after {
  border-left: 6px solid grey;
  right: 50%;
  margin-right: -17px;
}

.gym-time-container input[type="range"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  position: absolute;
  top: 50%;
  left: -25px;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  width: calc(100% + 50px);
  z-index: 2;
}

.gym-time-container input[type="range"]:focus,
.gym-time-container input[type="range"]:active {
  border: none;
  outline: none;
}

.gym-time-container input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background: transparent;
}

.gym-time-container input[type="range"]::-moz-range-track {
  -moz-appearance: none;
  height: 15px;
  width: 100%;
  background-color: transparent;
  position: relative;
  outline: none;
}

